import {Fragment, useState} from "react"
import MockDao from "../../MockDao/MockDao"
import RequestsViewAllListEntry from "./RequestsViewAllListEntry"
import './RequestsViewAllListEntry.css'

export default function RequestsViewAllList(props: any) {
    const [owner] = useState(props.empId !== undefined && props.empId === MockDao.getCurrentUserId())

    const getTitle = () => {
        if(!owner)
            return (<h3>requester: {props.empId}</h3>)    
    }

    const requestData = () => {
        if(!owner)
            return(<th>Requester</th>)
    }

    const getItems = () => {
        const items:any = [];
        //@ts-ignore
        props.data.forEach( (element, index) => {
            //console.log(element)
            //@ts-ignore
            items.push(<RequestsViewAllListEntry data={element} key={index+1} callback={handleClick} isOwner={owner}/>)
        }) 
        return(
            <Fragment>
            {getTitle()}
            <table><thead>
            <tr key={0}>
                <th>Id</th>                
                <th>Status</th>
                <th>Total</th>
                <th>Dates</th>
                {requestData()}
            </tr></thead><tbody>
            {items}
            </tbody></table>
            </Fragment>
        )
    }

    const handleClick = (formdata: any) => {
        console.log(formdata)
    }

    return (        
        <div className='RequestsViewAllList'>
            {getItems()}
        </div>
    );
}