import EmployeesViewListEntry from './EmployeesViewListEntry'

export default function EmployeesViewList(props: any) {
    /*
    let history = useHistory()    

    const handleClick = (event: any) => {
        console.log(event)

        
        history.push({
            pathname: '/EmployeeView',
        })
    }*/

    const getItems = () => {
        //console.log(props.data)
        const items: any = [];

        //if(props.data.length === undefined)
            //items.push(<EmployeeLine data={props.data} id={0} callback={handleClick}/>)

        //@ts-ignore
        var i:number;
        for(i = 0; i<props.data.length; i++) {
            let d = props.data[i]
            //console.log(d)
            items.push(<EmployeesViewListEntry data={d} key={i+1} />)
            //items.push(<EmployeesViewListEntry data={d} key={i} callback={handleClick}/>)
        }

        return (
            <table><thead>
                <tr key={0}>
                    <th>Id</th>
                    <th>First Name</th>
                    <th>Last Name</th>
                    <th>Email</th>
                    <th>Phone</th>
                    <th>Address</th>
                    <th>Type</th>
                </tr></thead><tbody>
            {items}
            </tbody></table>      
        )
    }

    return (        
        <div className='EmployeesViewList'>
            {getItems()}
        </div>
    );
}