import {Link} from 'react-router-dom';
import './SidebarManager.css';
import Sidebar from '../Sidebar/Sidebar';

export default function SidebarManager(props) {
    return (
        <div className="SidebarManager">
            <ul>            
            <li><Link to="/RequestsViewAll/all" className='nounderline'>Requests</Link></li>                  
            <li><Link to="/EmployeesView" className='nounderline'>Employees</Link></li>
            <li><Link to="/EmployeeCreate" className='nounderline'>Create Employee</Link></li>
            </ul>
            <Sidebar user={props.user}/>
        </div>
    );
}