import {Link} from 'react-router-dom';
import MockDao from '../../MockDao/MockDao'
import './Sidebar.css';

export default function Sidebar(props) {
    //let user = "/ProfileView/6097753963a78c2f8dff6f47"

    let id = MockDao.getCurrentUserId()

    return (
        <div className="Sidebar">
            <ul>            
            <li><Link to="/RequestCreate" className="nounderline">Request</Link></li>
            <li><Link to={"/RequestViewByEmployee/" + id} className="nounderline">Home</Link></li>
            <li><hr/></li>
            <li><Link to={"/ProfileView"} className="nounderline">Profile</Link></li>
            <li><Link to="/PasswordReset" className="nounderline">Password Reset</Link></li>
            <li><hr/></li>
            <li><Link to="/Logout" className="nounderline">Logout</Link></li>
            </ul>
        </div>
    );
}