import { Fragment, useRef } from "react"
import MockDao from "../../MockDao/MockDao"
import './Login.css'

export default function Login(props:any) {
    let loginRef = useRef<HTMLInputElement>(null)
    let passRef = useRef<HTMLInputElement>(null)

    const handleClick = (e: React.MouseEvent) => {
        e.preventDefault()

        const buttonId: string = (e.target as any).id
        const login: string = loginRef.current!.value
        const password: string = passRef.current!.value

        MockDao.getDao(storeCredentials, {[buttonId]: login, password: password})
    }

    function storeCredentials(result: any){
        let user = result.map
        MockDao.setUser(user)
        props.callback(user)
    }

    // function getLoginPassInputs() {
    //     return(
    //         <Fragment>
    //         <li><input ref={loginRef} type='text' placeholder='login...'></input></li>
    //         <li><input ref={passRef} type='text' placeholder='password...'></input></li>           
    //         </Fragment>
    //     )
    // }

    function getDebugLoginPassInputs() {
        return(
            <Fragment>
            <li><input ref={loginRef} type='text' defaultValue='abc@abc.com'></input></li>
            <li><input ref={passRef} type='text' defaultValue='pass'></input></li>     
            </Fragment>
        )
    }

    function getInputFields() {
        return (
            <ul>
            {
            //getLoginPassInputs()
            getDebugLoginPassInputs()
            }
            </ul>
        )
    }

    return (
        <div className="Login">
            <h2>Reinbursement Portal</h2>
            <form>
                {getInputFields()}
                <div className="loginButtons">
                    <button onClick={handleClick} id='login'>Employee Login</button>
                    <button onClick={handleClick} id='mgrLogin'>Manager Login</button>
                </div>
            </form>
        </div>
    )
}