import { Fragment } from "react";
import './RequestCreateForm.css'

export default function RequestCreateLine(props:any) {
    return(
        <Fragment>
            <tr key={props.id}>
                <td>{props.date}</td>
                <td>{props.expense}</td>
                <td className='enMoney'>{props.amount}</td>
                <td><span onClick={props.handleSubmit} id={props.id}>x</span></td>
            </tr>
        </Fragment>
    )
}